import { formatTokenName, g100 } from '@carbon/themes';
var theme = {}; // Rewrites the theme using CSS custom properties.

Object.keys(g100).forEach(function (token) {
  theme[token] = "var(--cds-".concat(formatTokenName(token), ", ").concat(g100[token], ")");
});
export default theme;